import {from, Observable, of, throwError as observableThrowError} from 'rxjs';
import {catchError, map} from 'rxjs/operators';

export class DevicesUtils {

  static getMediaDevices(): Observable<MediaDeviceInfo[]> {

    if (!navigator.mediaDevices || !navigator.mediaDevices.enumerateDevices) {
      console.error('Can\'t enumerate devices, method not supported.');
    }

    return from(navigator.mediaDevices.enumerateDevices())
      .pipe(
        map((devices: MediaDeviceInfo[]) => {
          const mediaDevices = [];
          for (const deviceI of devices) {
            console.log(deviceI);
            const device: any = {};
            for (const key in deviceI) {
              if (key) {
                device[key] = deviceI[key];
              }
            }
            if (!device.deviceId) {
              device.deviceId = (<any>device).id;
            }
            if (!device.label) {
              device.label = 'device (no-permission)';
            }
            mediaDevices.push(device);
          }
          return mediaDevices;
        })
      );
  }

  static getVideoDevices(): Observable<MediaDeviceInfo[]> {
    const videos =  DevicesUtils.getMediaDevices().pipe(
      map(devices => devices.filter(device => device.kind === 'videoinput'))
    );
    return videos;
  }

  static getAudioDevices(): Observable<MediaDeviceInfo[]> {
    const audios =  DevicesUtils.getMediaDevices().pipe(
      map(devices => devices.filter(device => device.kind === 'audioinput'))
    );
    return audios;
  }

  static playBeepSound() {
    const snd = new Audio('data:audio/wav;base64,');
    snd.play();
  }

  static getMediaStream(constrains: MediaStreamConstraints, deviceId: string): Observable<MediaStream> {

    if (!navigator.mediaDevices) {
      observableThrowError('Can\'t get user media, this is not supported.');
    }

    const video = deviceId === undefined
      ? {facingMode: {exact: 'environment'}}
      : {deviceId: {exact: deviceId}};

    constrains.video = video;

    return from(navigator.mediaDevices.getUserMedia(constrains))
      .pipe(
        map((stream: MediaStream) => stream),
        catchError(err => observableThrowError(err))
      );
  }

  static stopMedia(mediaStream: MediaStream) {
    if (mediaStream && mediaStream.getTracks) {
      mediaStream.getTracks()
        .forEach((track: MediaStreamTrack) => track.stop());
    }
  }

  static takeImage(videoEle: HTMLVideoElement, canvasEle: HTMLCanvasElement): Observable<any> {

    const dimensions = {};

    if (videoEle.videoWidth) {
      dimensions['width'] = videoEle.videoWidth;
      dimensions['height'] = videoEle.videoHeight;
    }

    canvasEle.width = dimensions['width'];
    canvasEle.height = dimensions['height'];

    canvasEle.getContext('2d').drawImage(videoEle, 0, 0);

    const mimeType = 'image/jpeg';
    const dataUrl = canvasEle.toDataURL(mimeType);

    return of({dataUrl, mimeType});
  }
}
