import {Observable} from 'rxjs';
import {delay, retryWhen, scan} from 'rxjs/operators';

export class RxUtils {

  public static retry<T>(count: number, wait: number): (source: Observable<T>) => Observable<T> {
    const tap: (source: Observable<T>) => Observable<T> = retryWhen(errors => errors.pipe(
      scan((acc, error) => {
        if (acc >= count || -1) {
          throw error;
        }
        return acc + 1;
      }, 0),
      delay(wait)
    ));
    return tap;
  }

}
