import {NotifyLevel, NotifyProp, NotifyType, StateNames} from '@er/types';
import {CommonsUtils} from '../comm';
import {StatesUtils} from '../states';

export class AnnounceUtils {

  static info(options: string | NotifyProp) {
    const notify = CommonsUtils.getObjWhenStr<NotifyProp>(options, 'message');
    StatesUtils.create(StateNames.notify, {
      ...notify,
      type: NotifyType.ANNOUNCE
    });
  }

  static error(options: string | NotifyProp) {
    const notify = CommonsUtils.getObjWhenStr<NotifyProp>(options, 'message');
    StatesUtils.create(StateNames.notify, <NotifyProp>{
      ...notify,
      level: NotifyLevel.ERROR,
      type: NotifyType.ANNOUNCE
    });
  }

  static success(options: string | NotifyProp) {
    const notify = CommonsUtils.getObjWhenStr<NotifyProp>(options, 'message');
    StatesUtils.create(StateNames.notify, <NotifyProp>{
      ...notify,
      level: NotifyLevel.SUCCESS,
      type: NotifyType.ANNOUNCE
    });
  }

  static warn(options: string | NotifyProp) {
    const notify = CommonsUtils.getObjWhenStr<NotifyProp>(options, 'message');
    StatesUtils.create(StateNames.notify, <NotifyProp>{
      ...notify,
      level: NotifyLevel.WARN,
      type: NotifyType.ANNOUNCE
    });
  }

}
