export class CacheUtils {

  private static cache: Map<string, Map<any, any>> = new Map();

  private static seq = 0;

  private static moduleId = 0;

  static getSeq() {
    return ++CacheUtils.seq;
  }

  static getModuleId() {
    return ++CacheUtils.moduleId;
  }

  static get<T = any>(type: string, key: any): T {
    const typeCache: Map<string, any> = CacheUtils.getTypeCache(type);
    if (!typeCache) {
      return null;
    }
    return typeCache.get(key);
  }

  static put<T = any>(type: string, key: any, data: T) {
    let typeCache = CacheUtils.getTypeCache(type);
    if (!typeCache) {
      typeCache = new Map();
      CacheUtils.cache.set(type, typeCache);
    }
    typeCache.set(key, data);
  }

  private static getTypeCache(type: string): Map<any, any> {
    return CacheUtils.cache.get(type);
  }
}
