import {ConfirmDialogProp, DynaContent, PopupDialogProp, StateNames} from '@er/types';
import {Observable} from 'rxjs';
import {filter, map} from 'rxjs/operators';
import {StatesUtils} from '../states';

export class DialogUtils {

  static confirm(header?: string, content?: string, options?: ConfirmDialogProp): Observable<boolean> {
    const state = StatesUtils.create(StateNames.confirm, {
      header,
      content,
      ...options,
      open: true
    });
    return state.pipe(
      filter(stateValue => stateValue && !stateValue.open),
      map(({accept}) => accept === true)
    );
  }

  static error(header, content, options?: PopupDialogProp) {
    const html = `
                 <div class="d-flex align-items-center">
                  <i class="fa fa-exclamation-circle fa-2x text-danger mr-2"></i>
                  <span style="font-size: 1.2em">${content}</span>
                </div>                 
    `;
    return DialogUtils.popup(header, html, {
      ...options || {},
      maximizable: false
    });
  }

  static info(header, content, options?: PopupDialogProp) {
    const html = `
                 <div class="d-flex align-items-center">
                  <i class="fa fa-info-circle fa-2x text-primary mr-2"></i>
                  <span style="font-size: 1.2em">${content}</span>
                </div>                 
    `;
    return DialogUtils.popup(header, html, {
      ...options || {},
      maximizable: false,
      minWidth: 200,
      closeLabel: 'ç¡®å®'
    });
  }

  private static popup(header?: string, content?: DynaContent, options?: PopupDialogProp): Observable<any> {
    const state = StatesUtils.create(StateNames.popup, {
      header,
      content,
      ...options,
      maximizable: false,
      visible: true
    });
    return state;
  }
}
