import {RegPatterns} from '@er/types';
import {CommonsUtils} from '../comm';

export class RegUtils {

  static escapeRegexp(regexStr: string): string {
    const result = regexStr.replace(/([.?*+^$[\]\\(){}|-])/g, '\\$1');
    return result;
  }

  static getRegexps(pattern: string | string[] | RegExp | RegExp[]): RegExp[] {
    if (!pattern) {
      return [];
    }
    const exps = [];
    const patterns = [];
    if (CommonsUtils.isString(pattern)) {
      patterns.push(pattern);
    } else {
      patterns.concat(pattern);
    }
    for (const p of patterns) {
      if (p) {
        if (p.indexOf(',') > 0) {
          exps.concat(RegUtils.getRegexps(p.split(',')));
        } else {
          exps.push(RegUtils.getRegexp(p));
        }
      }
    }
    return exps;
  }

  static test(src: string, pattern: string | RegExp | string[] | RegExp[] | { adn?: any[], or?: any[], not?: any[] }): any {
    let regex;
    if (typeof pattern === 'string') {
      regex = new RegExp(pattern);
    } else {
      regex = pattern;
    }
    if (regex instanceof RegExp) {
      return regex.test(src);
    } else if (regex && Array.isArray(regex)) {
      return regex.every((item: any) => {
        return RegUtils.test(src, item);
      });
    } else if (regex && Array.isArray(regex.and)) {
      return regex.and.every((item: any) => {
        return RegUtils.test(src, item);
      });
    } else if (regex && Array.isArray(regex.or)) {
      return regex.or.some((item: any) => {
        return RegUtils.test(src, item);
      });
    } else if (regex && regex.not) {
      return !RegUtils.test(src, regex.not);
    } else {
      return false;
    }
  }

  static exec(string: string, regex: any): any {
    const self = this;
    if (typeof regex === 'string') {
      regex = new RegExp(regex);
    }

    if (regex instanceof RegExp) {
      return regex.exec(string);
    } else if (regex && Array.isArray(regex)) {
      return regex.reduce(function (res: any, item: any) {
        return (!!res) ? res : self.exec(string, item);
      }, null);
    } else {
      return null;
    }
  }

  private static getRegexp(pattern: string | RegExp): RegExp {
    let exp: RegExp = null;
    if (!pattern) {
      return null;
    }
    if (CommonsUtils.isString(pattern)) {
      if (pattern === 'letter') {
        exp = RegPatterns.letter;
      } else if (pattern === 'digit') {
        exp = RegPatterns.digit;
      } else if (pattern === 'character') {
        exp = RegPatterns.character;
      } else if (pattern === 'integer') {
        exp = RegPatterns.integer;
      } else if (pattern === 'numeric') {
        exp = RegPatterns.numeric;
      } else {
        exp = new RegExp(pattern as string);
      }
    } else {
      exp = pattern as RegExp;
    }
    return exp;
  }
}
