import {State, Store, StoreItem} from '@er/types';
import {BehaviorSubject} from 'rxjs';
import {CommonsUtils} from '../comm';

const APP_STORE: BehaviorSubject<Store> = new BehaviorSubject({});

export class StatesUtils {

  static getStore(): BehaviorSubject<Store> {
    return APP_STORE;
  }

  static create(path: string | string[], value?: {}, preserved?: boolean): State {
    const storeItem = StatesUtils.getStoreItem(path, true);
    storeItem.state.next(value);
    if (preserved) {
      storeItem.preserved = preserved;
    }
    return storeItem.state;
  }

  static get(path: string | string[], createWhenAbsent?: boolean): BehaviorSubject<any> {
    const storeItem = StatesUtils.getStoreItem(path, createWhenAbsent);
    if (storeItem) {
      return storeItem.state;
    } else {
      return undefined;
    }
  }

  static observe(...path: string[]): BehaviorSubject<any> {
    return StatesUtils.get(path, true);
  }

  static toggle(...path: string[]): State {
    const state = StatesUtils.get(path);
    state.next(!!!state.value);
    return state;
  }

  static inc(path: string | string[]): State {
    const state = StatesUtils.get(path, true);
    let newValue = 0;
    if (state.value) {
      newValue = state.value + 1;
    }
    state.next(newValue);
    return state;
  }

  static dec(path: string | string[]): State {
    const state = StatesUtils.get(path, true);
    let newValue = 0;
    if (state.value) {
      newValue = state.value - 1;
    }
    state.next(newValue);
    return state;
  }

  static add(path: string | string[], offset: number): State {
    const state = StatesUtils.get(path, true);
    let newValue = 0;
    if (state.value) {
      newValue = state.value + offset;
    }
    state.next(newValue);
    return state;
  }

  /**
   * used to clear some state properties
   */
  static set(path: string | string[], final: { [key: string]: any }): State {
    const state = StatesUtils.get(path, true);
    if (!state.value) {
      return StatesUtils.create(path, final);
    } else {
      state.next(final);
      return state;
    }
  }

  static delete(path: string | string[], key: string | string[]) {
    const state = StatesUtils.get(path);
    if (state.value) {
      state.next(CommonsUtils.unset(state.value, key));
    }
  }

  /**
   * used to update state properties when patch has no undefined values
   */
  static update(path: string | string[], patch: { [key: string]: any }): State {
    const state = StatesUtils.get(path, true);
    if (!state.value) {
      return StatesUtils.create(path, patch);
    } else {
      state.next(CommonsUtils.merge({}, state.value, patch));
      return state;
    }
  }

  static getValue(...path: string[]): any {
    const state = StatesUtils.get(path);
    return state ? {...state.value} : undefined;
  }

  static deleteState(...path: string[]) {
    const state = StatesUtils.get(path);
    CommonsUtils.unset(APP_STORE.value, CommonsUtils.flatPath(path));
    if (state) {
      state.next(undefined);
      state.complete();
    }
    APP_STORE.next(APP_STORE.value);
  }

  private static getStoreItem(path: string | string[], createWhenAbsent?: boolean): StoreItem {
    const key = CommonsUtils.flatPath(path);
    let storeItem: StoreItem = CommonsUtils.get(APP_STORE.value, key);
    if (!storeItem && createWhenAbsent) {
      storeItem = {
        state: new BehaviorSubject(undefined),
        createdAt: new Date().getMilliseconds()
      };
      const value = CommonsUtils.set(APP_STORE.value, key, storeItem);
      APP_STORE.next(value);
    }
    return storeItem;
  }
}

