import {UI_COLORS} from '@er/types';
import {CommonsUtils} from '../comm';

let UI_COLORS_COUNT = -1;

export class ColorUtils {

  static getColorsCount() {
    if (UI_COLORS_COUNT === -1) {
      UI_COLORS_COUNT = Object.keys(UI_COLORS).length;
    }
    return UI_COLORS_COUNT;
  }

  static getColor(by?: string | number, offset = 0): string {
    let colorIndex;
    if (!by) {
      by = CommonsUtils.getRandomNumber(ColorUtils.getColorsCount());
    }
    if (CommonsUtils.isString(by)) {
      colorIndex = ((by as string).charCodeAt(0) + (by as string).charCodeAt((by as string).length - 1) + (by as string).length + offset) % ColorUtils.getColorsCount();
    } else {
      colorIndex = by;
    }
    let matched = 'pink';
    Object.keys(UI_COLORS).some((color, index) => {
      if (index === colorIndex) {
        matched = UI_COLORS[color];
        return true;
      } else {
        return false;
      }
    });
    return matched;
  }
}
