export class IdUtils {

  static getRandom(): string {
    return Math.floor(Math.random() * 10000000000) + '';
  }

  static getUUID(): string {
    if (typeof (window) !== 'undefined' &&
      typeof (window.crypto) !== 'undefined' &&
      typeof (window.crypto.getRandomValues) !== 'undefined') {
      const buf: Uint16Array = new Uint16Array(8);
      window.crypto.getRandomValues(buf);
      return (IdUtils._pad4(buf[0]) + IdUtils._pad4(buf[1])
        + '-' + IdUtils._pad4(buf[2])
        + '-' + IdUtils._pad4(buf[3])
        + '-' + IdUtils._pad4(buf[4])
        + '-' + IdUtils._pad4(buf[5]) + IdUtils._pad4(buf[6]) + IdUtils._pad4(buf[7]));
    } else {
      return IdUtils._random4() + IdUtils._random4()
        + '-' + IdUtils._random4()
        + '-' + IdUtils._random4()
        + '-' + IdUtils._random4()
        + '-' + IdUtils._random4() + IdUtils._random4() + IdUtils._random4();
    }
  }

  private static _pad4(num: number): string {
    let ret: string = num.toString(16);
    while (ret.length < 4) {
      ret = '0' + ret;
    }
    return ret;
  }

  private static _random4(): string {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  }

}
