import {ChartDimension, ChartType, DataItemProps} from '@er/types';
import {ColorUtils} from '../color';
import {CommonsUtils} from '../comm';
import {DataItemUtils} from '../data-item';

export class ChartUtils {

  static buildChartData(rawData: any, chartOptions: { type: ChartType, dimension?: ChartDimension, dataItemProps?: {} }, dimensionsIndex: number = 0) {
    const type = chartOptions.type;
    let data = rawData;
    const dimension = chartOptions.dimension || {};
    if (dimension.dataSrcPath) {
      data = CommonsUtils.get(rawData, dimension.dataSrcPath);
    }
    const dataItemProps = dimension.dataItemProps || chartOptions.dataItemProps;
    const label = dimension.title || '';
    switch (type || ChartType.BAR) {
      case ChartType.BAR:
        data = ChartUtils.buildBarChart(data, dataItemProps, label, dimensionsIndex);
        break;
      case ChartType.LINE:
        data = ChartUtils.buildLineChart(data, dataItemProps, label, dimensionsIndex);
        break;
      case ChartType.PIE:
        data = ChartUtils.buildPieChart(data, dataItemProps, label, dimensionsIndex);
        break;
      case ChartType.AREA:
        data = ChartUtils.buildAreaChart(data, dataItemProps, label, dimensionsIndex);
        break;
      case ChartType.RADAR:
        data = ChartUtils.buildRadarChart(data, dataItemProps, label, dimensionsIndex);
        break;
      case ChartType.DOUGHNUT:
        data = ChartUtils.buildPieChart(data, dataItemProps, label, dimensionsIndex);
        break;
    }
    return data;
  }

  static buildBarChart(dataItems: {}[], config: DataItemProps, label = '', colorIndex = 0) {
    const _data = DataItemUtils.transItems(dataItems, config);
    const labels = [];
    const datasets = [];
    const data = [];
    const backgroundColor = [];
    const hoverBackgroundColor = [];
    const items = _data;
    items.forEach(item => {
      if (item['group']) {
        datasets.push({
          label: items[''].label,
          data: items[''].items,
          backgroundColor: ColorUtils.getColor(items[''].label)
        });
        labels.push('');
      } else {
        const lb = item['key_as_string'] || item['label'];
        labels.push(lb);
        data.push(item['value']);
        backgroundColor.push(ColorUtils.getColor(lb, colorIndex));
        hoverBackgroundColor.push(ColorUtils.getColor(lb, colorIndex + 10));
      }
    });
    datasets.push({
      label: label,
      backgroundColor,
      hoverBackgroundColor,
      data
    });
    return {
      labels,
      datasets
    };
  }

  static buildPieChart(dataItems: {}[], config: DataItemProps, label = '', colorIndex = 0) {
    const _data = DataItemUtils.transItems(dataItems, config);
    const labels = [];
    const datasets = [];
    const data = [];
    const backgroundColor = [];
    const hoverBackgroundColor = [];
    const items = _data;
    items.forEach(item => {
      if (item['group']) {
        datasets.push({
          label: items[''].label,
          data: items[''].items,
          backgroundColor: ColorUtils.getColor(items[''].label)
        });
        labels.push('');
      } else {
        const lb = item['key_as_string'] || item['label'];
        labels.push(lb);
        data.push(item['value']);
        backgroundColor.push(ColorUtils.getColor(lb, colorIndex));
        hoverBackgroundColor.push(ColorUtils.getColor(lb, colorIndex + 10));
      }
    });
    datasets.push({
      label: label || '',
      backgroundColor,
      hoverBackgroundColor,
      data
    });
    return {
      labels,
      datasets
    };
  }

  static buildAreaChart(dataItems: {}[], config: DataItemProps, label = '', colorIndex = 0) {
    const _data = DataItemUtils.transItems(dataItems, config);
    const labels = [];
    const datasets = [];
    const backgroundColor = [];
    const data = [];
    const items = _data;
    items.forEach(item => {
      if (item['group']) {
        datasets.push({
          label: items[''].label,
          data: items[''].items,
          backgroundColor: ColorUtils.getColor(items[''].label)
        });
        labels.push('');
      } else {
        const lb = item['key_as_string'] || item['label'];
        labels.push(lb);
        data.push(item['value']);
        backgroundColor.push(ColorUtils.getColor(lb, colorIndex));
      }
    });
    datasets.push({
      label: label || '',
      data,
      backgroundColor
    });
    return {
      labels,
      datasets
    };
  }

  static buildLineChart(dataItems: {}[], config: DataItemProps, label = '', colorIndex = 0) {
    const _data = DataItemUtils.transItems(dataItems, config);
    const labels = [];
    const datasets = [];
    const data = [];
    const items = _data;
    items.forEach(item => {
      if (item['group']) {
        datasets.push({
          label: items[''].label,
          data: items[''].items,
          backgroundColor: ColorUtils.getColor(items[''].label)
        });
        labels.push('');
      } else {
        const lb = item['key_as_string'] || item['label'];
        labels.push(lb);
        data.push(item['value']);
      }
    });
    datasets.push({
      label: label || '',
      data,
      fill: false,
      borderColor: ColorUtils.getColor(colorIndex)
    });
    return {
      labels,
      datasets
    };
  }

  static buildRadarChart(dataItems: {}[], config: DataItemProps, label = '', colorIndex = 0) {
    const _data = DataItemUtils.transItems(dataItems, config);
    const labels = [];
    const datasets = [];
    const backgroundColor = [];
    const borderColor = [];
    const pointBackgroundColor = [];
    const pointHoverBorderColor = [];
    const data = [];
    const items = _data;
    items.forEach(item => {
      if (item['group']) {
        datasets.push({
          label: items[''].label,
          data: items[''].items,
          backgroundColor: ColorUtils.getColor(items[''].label)
        });
        labels.push('');
      } else {
        const lb = item['key_as_string'] || item['label'];
        labels.push(lb);
        data.push(item['value']);
        backgroundColor.push(ColorUtils.getColor(lb, colorIndex));
        borderColor.push(ColorUtils.getColor(lb, colorIndex + 5));
        pointBackgroundColor.push(ColorUtils.getColor(lb, colorIndex + 5));
        pointHoverBorderColor.push(ColorUtils.getColor(lb, colorIndex + 5));
      }
    });
    datasets.push({
      label: label || '',
      data,
      backgroundColor,
      borderColor,
      pointBackgroundColor,
      pointHoverBorderColor,
      pointBorderColor: '#fff',
      pointHoverBackgroundColor: '#fff'
    });
    return {
      labels,
      datasets
    };
  }
}
