import {StateNames} from '@er/types';
import * as momentNs from 'moment';
import {CommonsUtils} from '../comm';
import {StatesUtils} from '../states';

const moment = momentNs;

export class DateUtils {

  static getMoment(data): momentNs.Moment {
    if (moment.isMoment(data)) {
      return data;
    }
    const date = DateUtils.toDate(data);
    if (date) {
      return moment(data);
    }
    return null;
  }

  static isDate(v) {
    return !!DateUtils.toDate(v);
  }

  static toDate(v) {
    if (CommonsUtils.isString(v)) {
      const date = Date.parse(v);
      if (!isNaN(date)) {
        return new Date(date);
      }
    } else {
      if (v && typeof v.getMonth === 'function') {
        return v;
      }
    }
    return undefined;
  }

  static getFormattedDate(date?, format = 'YYYY-MM-DD') {
    return DateUtils.getFormattedDateTime(format, date);
  }

  static getFormattedTime(date?, format = 'HH:mm:ss') {
    return DateUtils.getFormattedDateTime(format, date);
  }

  static getFormattedDateTime(format = 'YYYY-MM-DD HH:mm:ss', date?) {
    if (!date) {
      date = DateUtils.getDate();
    } else {
      date = DateUtils.toDate(date);
    }
    const momentData = DateUtils.getMoment(date);
    if (momentData) {
      return momentData.format(format);
    }
    return '';
  }

  static getDate() {
    let date;
    const dateState = StatesUtils.getValue(StateNames.serverTime);
    if (dateState) {
      date = dateState.date;
    } else {
      date = new Date();
    }
    return date;
  }

  static getStamp() {
    return DateUtils.getDate().getTime();
  }

  static getDateByNow(present, due?): { seconds: number, message: string } {
    if (!present) {
      return {seconds: 0, message: ''};
    }
    if (!DateUtils.isDate(present)) {
      try {
        present = new Date(present);
      } catch (e) {
        present = null;
      }
    }
    if (!present) {
      return {seconds: 0, message: ''};
    }
    let _due = due;
    if (!_due) {
      _due = new Date();
    }
    if (!DateUtils.isDate(_due)) {
      _due = new Date(_due);
    }
    const seconds = Math.round(Math.abs((_due.getTime() - present.getTime()) / 1000));
    const minutes = Math.round(Math.abs(seconds / 60));
    const hours = Math.round(Math.abs(minutes / 60));
    const days = Math.round(Math.abs(hours / 24));
    const months = Math.round(Math.abs(days / 30.416));
    const years = Math.round(Math.abs(days / 365));
    let message = '';
    if (seconds <= 10) {
      message = (_due > present) ? 'åå' : 'é©¬ä¸';
    } else if (seconds <= 30) {
      message = '30ç§';
    } else if (seconds <= 90) {
      message = '1åé';
    } else if (minutes <= 45) {
      message = minutes + 'åé';
    } else if (minutes <= 90) {
      message = '1å°æ¶';
    } else if (hours <= 22) {
      message = hours + 'å°æ¶';
    } else if (hours <= 36) {
      message = '1å¤©';
    } else if (days <= 25) {
      message = days + 'å¤©';
    } else if (days <= 45) {
      message = '1ä¸ªæ';
    } else if (days <= 345) {
      message = months + 'æ';
    } else if (days <= 545) {
      message = '1å¹´';
    } else { // (days > 545)
      message = years + 'å¹´';
    }
    if (seconds > 10) {
      message += (_due > present) ? 'å' : 'å';
    }
    return {
      seconds,
      message
    };
  }

  static getUpdateSeconds(seconds: number) {
    const min = 60;
    const hr = min * 60;
    const day = hr * 24;
    if (seconds < min) { // less than 1 min, update every 2 secs
      return 2;
    } else if (seconds < hr) { // less than an hour, update every 30 secs
      return 30;
    } else if (seconds < day) { // less then a day, update every 5 mins
      return 300;
    } else { // update every hour
      return 3600;
    }
  }

  static getYear(date?: Date) {
    return (date || DateUtils.getDate()).getFullYear();
  }

  static getMonth(date?: Date) {
    return (date || DateUtils.getDate()).getMonth() + 1;
  }

  static getDay(date?: Date) {
    return (date || DateUtils.getDate()).getDate();
  }

  static getYesterday() {
    return DateUtils.getDay(new Date(DateUtils.getDate().getTime() - 24 * 60 * 60 * 1000));
  }

  static getTomorrow() {
    return DateUtils.getDay(new Date(DateUtils.getDate().getTime() + 24 * 60 * 60 * 1000));
  }

  static getLastMonth() {
    const month = DateUtils.getMonth() - 1;
    return month < 1 ? 12 : month;
  }

  static getNextMonth() {
    const month = DateUtils.getMonth() + 1;
    return month > 12 ? 1 : month;
  }
}
