import pinyinlite from 'pinyinlite/index_full';

export class PinyinUtil {
  /**
   * è·åæ±å­çæ¼é³é¦å­æ¯
   * @param str æ±å­å­ç¬¦ä¸²ï¼å¦æéå°éæ±å­ååæ ·è¿å
   */
  static getFirstLetter(str): string {
    const pys: string[] = pinyinlite(str);
    const fpy = pys.map(py => py[0] && py[0].substr(0, 1));
    return fpy.join('').toLowerCase();
  }

  static getPinYin(str): string {
    const pys: string[] = pinyinlite(str);
    const fpy = pys.map(py => py[0] ? py[0] : '');
    return fpy.join('').toLowerCase();
  }
}
